.animated {
  position: absolute;
  width: 150px;
  height: 150px;
  background-color: rgb(229, 228, 215);
}
.animated.box {
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}
.animated.box_todo_1 {
  top: 40px;
  right: 0;
}
.animated.box_todo_3 {
  right: 0;
  bottom: 20px;
  background-color: transparent;
  border: 5px solid rgba(104, 62, 50, 0.681);
}
.animated.box_todo_2 {
  bottom: 20px;
  left: 0;
}
.animated.box_todo_4 {
  bottom: 80px;
  left: 120px;
  background-color: transparent;
  border: 5px solid rgba(104, 62, 50, 0.681);
  animation-name: moveframeboxfour;
  animation-duration: 5s;
}
@keyframes moveframeboxfour {
  to {
    bottom: 100px;
    left: 150px;
  }
}
.animated.box_todo_5 {
  right: 200px;
  bottom: 30px;
  width: 100px;
  height: 100px;
  animation-name: move_todo_bottom;
  animation-duration: 7s;
}
@keyframes move_todo_bottom {
  to {
    right: 100px;
    bottom: 30px;
  }
}
.animated.box_1 {
  width: 400px;
  height: 400px;
  top: 0;
  right: 0;
}
.animated.box_3 {
  right: 150px;
  top: 150px;
  width: 250px;
  height: 250px;
  background-color: transparent;
  border: 5px solid rgba(104, 62, 50, 0.681);
  animation-name: moveframebox;
  animation-duration: 7s;
}
@keyframes moveframebox {
  to {
    right: 240px;
    top: 240px;
  }
}
.animated.box_2 {
  bottom: 0;
  left: 50px;
  width: 250px;
  height: 250px;
}
.animated.box_4 {
  bottom: 80px;
  left: 120px;
  width: 250px;
  height: 250px;
  background-color: transparent;
  border: 5px solid rgba(104, 62, 50, 0.681);
  animation-name: moveframeboxfour;
  animation-duration: 5s;
}
@keyframes moveframeboxfour {
  to {
    bottom: 100px;
    left: 150px;
  }
}
.animated.box_5 {
  top: 200px;
  left: 100px;
  width: 100px;
  height: 100px;
  animation-name: moverightbox;
  animation-duration: 8s;
}
@keyframes moverightbox {
  to {
    top: 200px;
    left: 300px;
  }
}